<template>
  <div>
    <v-navigation-drawer v-if="isSidebarOpen" permanent theme="light" class="modern-sidebar">
      <v-list-item height="60" class="sidebar-header">
        <v-img 
          :src="require('../../public/LogoGQTC.png')" 
          height="40"
          width="150"
          contain
          class="logo"
        ></v-img>
        <v-spacer></v-spacer>
        <v-btn variant="text" icon="mdi-menu" @click="toggleSidebar" class="toggle-btn"></v-btn>
      </v-list-item>
      <v-list density="default" nav class="sidebar-list">
        <v-list-item v-for="item in menuItems" :key="item.title" :to="item.to" :prepend-icon="item.icon" :title="item.title"
          :value="item.value" class="sidebar-item" :class="{ 'active-item': $route.name === item.to.name }">
        </v-list-item>

        <v-list-group v-for="group in visibleMenuGroups" :key="group.title" class="sidebar-group">
          <template v-slot:activator="{ props }">
            <v-list-item v-bind="props" :prepend-icon="group.icon" :title="group.title" :value="group.value"
              class="sidebar-group-header"></v-list-item>
          </template>
          <v-list-item v-for="subItem in group.items" :key="subItem.title" :to="subItem.to" :title="subItem.title"
            :value="subItem.value" class="sidebar-subitem"></v-list-item>
        </v-list-group>

        <v-list-item prepend-icon="mdi-logout" title="Log Out" value="logout" @click="logoutWarn" class="logout-item"></v-list-item>
      </v-list>
    </v-navigation-drawer>

    <!-- Main content area -->
    <v-container fluid class="pa-0">
      <!-- Toggle button when sidebar is closed -->
      <v-btn v-if="!isSidebarOpen" icon="mdi-menu" @click="toggleSidebar" class="floating-toggle-btn"></v-btn>
      <deleteWarnVue :visible="log_dialog" @close="log_dialog = false" :item="deleteValue" @delete="logOut" :log="true" />
      <router-view />
    </v-container>
  </div>
</template>

<script>
import axios from 'axios';
import deleteWarnVue from './deleteWarn.vue';

export default {
  name: 'Sidebar',
  components: { deleteWarnVue },
  data: () => ({
    isSidebarOpen: true,
    log_dialog: false,
    menuItems: [
      { title: 'Dashboard', icon: 'mdi-view-dashboard-outline', to: { name: 'dash' }, value: 'dashboard' },
      { title: 'Category', icon: 'mdi-view-list-outline', to: { name: 'category' }, value: 'category' },
      { title: 'Clients', icon: 'mdi-account-outline', to: { name: 'client' }, value: 'clients' },
      { title: 'Equipment', icon: 'mdi-vector-difference', to: { name: 'equipment' }, value: 'equipment' },
      { title: 'Services', icon: 'mdi-hammer-wrench', to: { name: 'serviceRequest' }, value: 'services' }
    ],
    menuGroups: [
      {
        title: 'Reminders', icon: 'mdi-alarm', value: 'reminders',
        items: [
          { title: 'Preventive Reminders', to: { name: 'preventiveReminders' }, value: 'preventiveReminders' },
           // { title: 'Calibration Reminders', to: { name: 'calibrationReminders' }, value: 'calibrationReminders' },
        ],
      },
      {
        title: 'Reports', icon: 'mdi-file-chart-outline', value: 'reports',
        items: [
          { title: 'Client Report', to: { name: 'clientReport' }, value: 'clientReport' },
          { title: 'Equipment Report', to: { name: 'equipmentReport' }, value: 'equipmentReport' },
          { title: 'Service Report', to: { name: 'servicetReport' }, value: 'servicetReport' },
          { title: 'Technician Report', to: { name: 'technicianReport' }, value: 'technicianReport' },
        ],
      },
      {
        title: 'Users', icon: 'mdi-account-group-outline', value: 'users',
        items: [
          { title: 'Admin', to: { name: 'admin' }, value: 'admin' },
          { title: 'Sub Admin', to: { name: 'subAdmin' }, value: 'subAdmin' },
          { title: 'Technician', to: { name: 'technician' }, value: 'technician' },
        ],
        visible: () => JSON.parse(localStorage.getItem('user'))?.role[0] === 'admin',
      },
      {
        title: 'Settings', icon: 'mdi-cog-outline', value: 'settings',
        items: [
          { title: 'City', to: { name: 'cityPage' }, value: 'city' },
          { title: 'Care Of', to: { name: 'careOfPage' }, value: 'careOff' },
          // { title: 'Service Type', to: { name: 'serviceType' }, value: 'serviceType' },
        ],
        visible: () => JSON.parse(localStorage.getItem('user'))?.role[0] !== 'Engineer',
      },
    ],
  }),
  computed: {
    visibleMenuGroups() {
      return this.menuGroups.filter(group => !group.visible || group.visible());
    },
  },
  methods: {
    toggleSidebar() {
      this.isSidebarOpen = !this.isSidebarOpen;
    },
    logoutWarn() {
      this.log_dialog = true;
    },
    async logOut() {
      const auth = JSON.parse(localStorage.getItem('user'));
      await axios.get(`${process.env.VUE_APP_BASE_URL}auth/log_out`, {
        headers: { Authorization: 'Bearer ' + auth.access_token },
      }).then(() => {
        localStorage.clear();
        this.$router.push({ name: 'signIn' });
      });
    },
  },
};
</script>

<style scoped>
/* Sidebar Header */
.sidebar-header {
  background: linear-gradient(90deg, #ecc2a9, #e45b2d); /* Primary to lighter secondary */
}

.logo {
  max-height: 40px;
  width: 150px;
  object-fit: contain;
}

.toggle-btn {
  color: #fff !important;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transition: transform 0.3s ease, background 0.3s ease;
}

.toggle-btn:hover {
  transform: rotate(180deg);
  background: rgba(255, 255, 255, 0.3);
}

/* Floating Toggle Button */
.floating-toggle-btn {
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 1000;
  background: linear-gradient(45deg, #FF5733, #FF8E53);
  color: #fff;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  box-shadow: 0 6px 20px rgba(255, 87, 51, 0.6);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  animation: bounceIn 0.8s ease-out;
}

.floating-toggle-btn:hover {
  transform: scale(1.15);
  box-shadow: 0 8px 25px rgba(255, 87, 51, 0.8);
}

/* Sidebar List */
.sidebar-list {
  padding: 25px;
  font-family: 'Montserrat', sans-serif;
  animation: fadeInLeft 0.8s ease-out;
  position: relative;
  z-index: 1;
}

/* Sidebar Items */
.sidebar-item, .sidebar-group-header {
  border-radius: 15px;
  margin-bottom: 15px;
  padding: 14px;
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.sidebar-item::before, .sidebar-group-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: left 0.5s ease;
}

.sidebar-item:hover::before, .sidebar-group-header:hover::before {
  left: 100%;
}

.sidebar-item:hover, .sidebar-group-header:hover {
  transform: translateX(10px);
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 6px 20px rgba(255, 255, 255, 0.3);
}

.active-item {
  background: linear-gradient(45deg, #FF8E53, #FF5733); /* Secondary to Primary */
}

/* Subitems */
.sidebar-subitem {
  color: #1c3869;
  padding-left: 60px;
  border-radius: 12px;
  transition: all 0.3s ease;
  position: relative;
  animation: fadeInUp 0.5s ease-out;
  background: rgba(255, 255, 255, 0.05);
}

.sidebar-subitem:hover {
  background: rgba(255, 255, 255, 0.25);
  transform: translateX(8px);
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.2);
}

/* Logout Item */
.logout-item {
  background: linear-gradient(45deg, #2A4066, #FF5733); /* Secondary to Primary */
  color: #fff;
  border-radius: 15px;
  margin-top: 40px;
  padding: 14px;
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
  animation: bounceIn 0.8s ease-out;
  box-shadow: 0 4px 15px rgba(255, 87, 51, 0.5);
}

.logout-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.5), transparent);
  transition: left 0.5s ease;
}

.logout-item:hover::before {
  left: 100%;
}

.logout-item:hover {
  background: linear-gradient(45deg, #FF5733, #2A4066); /* Reversed */
  transform: scale(1.05);
  box-shadow: 0 8px 30px rgba(255, 87, 51, 0.8);
}

/* CSS Keyframe Animations */
@keyframes slideInLeft {
  0% {
    transform: translateX(-100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInDown {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 87, 51, 0.7);
  }
  70% {
    box-shadow: 0 0 0 15px rgba(255, 87, 51, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 87, 51, 0);
  }
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}

/* Mobile Responsiveness */
@media (max-width: 960px) {
  .modern-sidebar {
    width: 220px;
  }

  .sidebar-item, .sidebar-group-header, .sidebar-subitem {
    padding: 12px;
  }

  .sidebar-subitem {
    padding-left: 40px;
  }

  .logout-item {
    margin-top: 30px;
  }

  .floating-toggle-btn {
    top: 15px;
    left: 15px;
    width: 45px;
    height: 45px;
  }
  
  .logo {
    max-height: 35px;
    width: 120px;
  }
}

@media (max-width: 600px) {
  .modern-sidebar {
    width: 200px;
  }

  .sidebar-header {
    padding: 12px;
  }

  .logo {
    max-height: 30px;
    width: 100px;
  }

  .sidebar-item, .sidebar-group-header, .sidebar-subitem {
    font-size: 0.9rem;
    padding: 10px;
  }

  .sidebar-subitem {
    padding-left: 30px;
  }

  .floating-toggle-btn {
    width: 40px;
    height: 40px;
    top: 10px;
    left: 10px;
  }
}
</style>