import axios from 'axios'
export default {
    namespaced: true,
    state: {
        saleEquipmentList: [],
        saleEquipmentReport: {},
        editValue: {},
        serialList: {},
        saleTotalPage: ''
        // allEquipments:{}
    },
    mutations: {
        SET_SALE_EQUIPMENTS(state, data) {
            state.saleEquipmentList = data
        },
        SET_SALE_EQUIPMENTS_REPORT_DATA(state, data) {
            state.saleEquipmentReport = data
        },
        REMOVE_SALE_EQUIPMENTS(state) {
            state.saleEquipmentList = {}
        },
        SET_EDIT_SALE_EQUIPMET_VALUE(state, data) {
            state.editValue = data
        },
        SET_SALE_EQUIMENT_TOTAL_PAGE(state, data) {
            state.saleTotalPage = data
        },
        SET_SALE_EQUIPMENT_BY_EQUIPMENT_ID_AND_CLIENT_ID(state, data) {
            state.serialList = data
        }
        // SET_ALL_EQUIPMENTS(state, data){
        //     state.allEquipments = data
        // }
    },
    actions: {
        async GET_ALL_SALE_EQUIPMENT({ commit, state }, params) {

            const auth = JSON.parse(localStorage.getItem('user'));
            const options = {
                method: 'get',
                url: `${process.env.VUE_APP_BASE_URL}sales/list_of_sales`,

                headers: {
                    Authorization: 'Bearer ' + auth.access_token
                }

            }
            if (params) {
                options.params = params
            }
            const data = await axios(options)

            console.log(data.data.data)
            commit('SET_SALE_EQUIPMENTS', data.data.data);

            commit('SET_SALE_EQUIMENT_TOTAL_PAGE', data.data.totalPages)
            //  console.log(data.data.totalPages) 

        },
        async GET_ALL_SALE_EQUIPMENT_FOR_REPORT({ commit, state }, params) {
            const auth = JSON.parse(localStorage.getItem('user'));
            const options = {
                method: 'get',
                url: `${process.env.VUE_APP_BASE_URL}sales/list_of_sales`,
                headers: {
                    Authorization: 'Bearer ' + auth.access_token
                }

            }
            if (params) {
                options.params = params
            }
            const data = await axios(options)

            // console.log(data)
            commit('SET_SALE_EQUIPMENTS_REPORT_DATA', data.data.data);

            // commit('SET_SALE_EQUIMENT_TOTAL_PAGE', data.data.totalPages)
            //  console.log(data.data.totalPages) 

        },
        async ADD_SALE_EQUIPMENT({ commit }, payload) {
            // console.log(payload);
            try {

                const auth = JSON.parse(localStorage.getItem('user'));

                const data = new FormData();
                data.append("serialNo", payload.serialNo);
                data.append("equipmentId", payload.equipmentId);
                data.append("clientId", payload.clientId);
                // data.append("callibrationPeriod", payload.callibrationPeriod);
                data.append("maintenancePeriod", payload.maintenancePeriod);
                data.append("purchaseDate", payload.purchaseDate);
                // data.append("orderDate", payload.orderDate);
                data.append("installationDate", payload.installationDate);
                // data.append("LastCallibrationDate", payload.LastCallibrationDate);
                data.append("LastMaintenanceDate", payload.LastMaintenanceDate);
                data.append("warrantyDate", payload.warrantyDate);
                data.append("invoice", payload.invoice[0]);
                data.append("installationReport", payload.installationReport[0]);
                data.append("warrantyDocument", payload.warrantyDocument[0]);
                data.append("maintenanceContract", payload.maintenanceContract[0]);
                data.append("annualMaintenanceContract", payload.annualMaintenanceContract[0]);

                const addEquipment = await axios({
                    method: 'post',
                    url: `${process.env.VUE_APP_BASE_URL}sales/add_sales`,
                    data: data,
                    headers: {
                        Authorization: 'Bearer ' + auth.access_token
                    }

                })
                // console.log(addEquipment);

            } catch (error) {
                console.log(error);
            }


        },
        async DELETE_SALE_EQUIPMENT({ commit }, payload) {
            // console.log(payload)
            try {
                const auth = JSON.parse(localStorage.getItem('user'));
                const deleteSaleEquipment = await axios({
                    method: 'get',
                    url: `${process.env.VUE_APP_BASE_URL}sales/delete_sales/${payload}`,

                    headers: {
                        Authorization: 'Bearer ' + auth.access_token
                    }

                })
                // console.log(deleteSaleEquipment)

            } catch (error) {
                console.log(error)
            }
        },
        async GET_SALE_EQUIPMENT_BY_ID({ commit, state }, payload) {
            // console.log(payload)
            try {
                const auth = JSON.parse(localStorage.getItem('user'));
                const saleEquipmentWithId = await axios({
                    method: 'get',
                    url: `${process.env.VUE_APP_BASE_URL}sales/list_of_sales`,
                    params: {
                        sid: payload.id
                    },
                    headers: {
                        Authorization: 'Bearer ' + auth.access_token
                    }

                })
                commit('SET_EDIT_SALE_EQUIPMET_VALUE', saleEquipmentWithId.data.data[0])
                //  console.log(saleEquipmentWithId.data)
                 console.log(state.editValue)

            } catch (error) {
                console.log(error)
            }
        },
        async UPDATE_SALE_EQUIPMET({ commit }, payload) {
            try {
                // console.log(payload)
                const auth = JSON.parse(localStorage.getItem('user'));
                const data = new FormData()
                data.append("serialNo", payload.serialNo);
                data.append("equipmentId", payload.equipmentId);
                data.append("clientId", payload.clientId);
                // if (payload.callibrationPeriod) {
                //     data.append("callibrationPeriod", payload.callibrationPeriod);
                // }
                if (payload.maintenancePeriod) {
                    data.append("maintenancePeriod", payload.maintenancePeriod);
                }
                data.append("purchaseDate", payload.purchaseDate);
                // data.append("orderDate", payload.orderDate);
                data.append("installationDate", payload.installationDate);
                // data.append("LastCallibrationDate", payload.LastCallibrationDate);
                data.append("LastMaintenanceDate", payload.LastMaintenanceDate);
                data.append("warrantyDate", payload.warrantyDate);
                if (payload.invoice) {
                    data.append("invoice", payload.invoice[0]);
                }
                if (payload.installationReport) {
                    data.append("installationReport", payload.installationReport[0]);
                }
                if (payload.warrantyDocument) {
                    data.append("warrantyDocument", payload.warrantyDocument[0]);
                }
                if (payload.maintenanceContract) {
                    data.append("maintenanceContract", payload.maintenanceContract[0]);
                }
                if (payload.annualMaintenanceContract) {
                    data.append("annualMaintenanceContract", payload.annualMaintenanceContract[0]);
                }
                const update = await axios({
                    method: 'post',
                    url: `${process.env.VUE_APP_BASE_URL}sales/update_sales/ ${payload.id}`,
                    data: data,
                    headers: {
                        Authorization: 'Bearer ' + auth.access_token
                    }

                })
                // console.log(update)

            } catch (error) {
                console.log(error)
            }
        },
        async EQUIPMENT_FILTER({ commit, state }, params) {
            const auth = JSON.parse(localStorage.getItem('user'));
            await axios({
                method: 'get',
                url: `${process.env.VUE_APP_BASE_URL}sales/all_sales`,
                headers: {
                    Authorization: 'Bearer ' + auth.access_token
                },
                params: params

            }).then((res) => {
                commit('SET_SALE_EQUIPMENTS', res.data.data)
                // console.log(state.allEquipments)
            }).catch(err => {
                console.log(err);
            })
        },
        async GET_SALE_EQUIPMENT_BY_EQUIPMENT_ID_AND_CLIENT_ID({ commit, state }, params) {
            const auth = JSON.parse(localStorage.getItem('user'));
            await axios({
                method: 'get',
                url: `${process.env.VUE_APP_BASE_URL}sales/list_of_sales`,
                headers: {
                    Authorization: 'Bearer ' + auth.access_token
                },
                params: params

            }).then((res) => {
                commit('SET_SALE_EQUIPMENT_BY_EQUIPMENT_ID_AND_CLIENT_ID', res.data.data)
                // console.log(state.serialList)
            }).catch(err => {
                console.log(err);
            })
        }

    },
    getters: {
        get_all_equipment_with_clientId_and_equiId: (state) => (clId, equId) => {
            return state.saleEquipmentList.filter((state) => state.equipmentId === equId && state.clientId === clId)
        },
        get_sale_by_serialNo: (state) => (serial) => {
            return state.saleEquipmentList.find((state) => state.serialNo === serial)
        }

    }

}