<template>
    <v-container class="px-4 hmmsdashboard" fluid style="min-height: 100vh; background: linear-gradient(135deg, #F5F7FA, #E8ECEF);">
      <!-- Breadcrumb Navigation -->
      <v-breadcrumbs :items="breadcrumbs" class="breadcrumbs-container">
        <template v-slot:divider>
          <v-icon color="#FF5733">mdi-chevron-right</v-icon>
        </template>
        <template v-slot:item="{ item }">
          <v-breadcrumbs-item :href="item.href" :disabled="item.disabled" class="custom-breadcrumb-item">
            {{ item.text.toUpperCase() }}
          </v-breadcrumbs-item>
        </template>
      </v-breadcrumbs>
  
      <div class="text-h6 mb-2 category-title" style="font-family: 'Montserrat', sans-serif !important">
        PREVENTIVE MAINTENANCE REMINDER
      </div>
  
      <!-- Search and Filter Toolbar -->
      <searchAndFilterToolbar 
        :auto="true" 
        :show-filter-icon="false" 
        :placeholder="'Search with Client and Equipment Name'" 
        @btn_action="openAddService" 
        @fiterWithName="searchByName" 
        @filterBtn="filterDisplay = !filterDisplay" 
        class="toolbar" 
      />
  
      <!-- Advanced Filters -->
      <Transition name="slide-fade">
        <div class="mt-4 mb-2 filter-container" v-if="filterDisplay">
          <v-row>
            <v-col cols="12" sm="6" md="3">
              <v-autocomplete 
                v-model="selectedClient" 
                clearable 
                hide-details 
                variant="outlined" 
                density="compact" 
                :items="clientList" 
                item-title="name" 
                item-value="id" 
                label="Client" 
                @change="applyFilters"
              ></v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="3">
              <v-autocomplete 
                v-model="selectedEquipment" 
                clearable 
                hide-details 
                variant="outlined" 
                density="compact" 
                :items="allEquipments" 
                item-title="equipmentName" 
                item-value="id" 
                label="Equipment" 
                @change="applyFilters"
              ></v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="3">
              <v-autocomplete 
                v-model="selectedCity" 
                clearable 
                hide-details 
                variant="outlined" 
                density="compact" 
                :items="cityList" 
                item-title="city" 
                item-value="id" 
                label="City" 
                @change="applyFilters"
              ></v-autocomplete>
            </v-col>
            <v-col cols="12" sm="6" md="3" class="d-flex align-center">
              <v-btn 
                class="filter-btn" 
                style="background: linear-gradient(45deg, #FF5733, #2A4066); color: white;" 
                @click="applyFilters"
              >
                Filter
              </v-btn>
              <v-btn 
                class="ml-2" 
                variant="text" 
                color="#FF5733" 
                @click="resetFilters"
              >
                Clear
              </v-btn>
            </v-col>
          </v-row>
        </div>
      </Transition>
  
      <!-- Maintenance Reminder Tables by Time Range -->
      <v-row class="mt-4">
        <!-- 0-30 Days -->
        <v-col cols="12">
          <h3 class="section-title">0-30 Days</h3>
          <v-table v-if="range0to30.length" class="modern-table rounded-lg">
            <thead>
              <tr>
                <th class="text-left">#</th>
                <th class="text-left">Equipment</th>
                <th class="text-left">Due Date (days remaining/ago)</th>
                <th class="text-left">Client</th>
                <th class="text-left">City</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, i) in range0to30" :key="item.id" class="table-row-hover">
                <td>{{ i + 1 }}</td>
                <td>{{ item.sale.equipment.equipmentName }}</td>
                <td>
                  {{ item.maintenancePeriod.split('T')[0] }}
                  ({{ getDaysDisplay(item.maintenancePeriod.split('T')[0]) }})
                </td>
                <td>{{ item.sale.client.name }}</td>
                <td>{{ item.sale.client.city.city }}</td>
              </tr>
            </tbody>
          </v-table>
          <v-alert v-else type="info" class="mt-2">No data</v-alert>
        </v-col>
  
        <!-- 30-60 Days -->
        <v-col cols="12">
          <h3 class="section-title">30-60 Days</h3>
          <v-table v-if="range30to60.length" class="modern-table rounded-lg">
            <thead>
              <tr>
                <th class="text-left">#</th>
                <th class="text-left">Equipment</th>
                <th class="text-left">Due Date (days remaining)</th>
                <th class="text-left">Client</th>
                <th class="text-left">City</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, i) in range30to60" :key="item.id" class="table-row-hover">
                <td>{{ i + 1 }}</td>
                <td>{{ item.sale.equipment.equipmentName }}</td>
                <td>
                  {{ item.maintenancePeriod.split('T')[0] }}
                  ({{ getDaysRemaining(item.maintenancePeriod.split('T')[0]) }})
                </td>
                <td>{{ item.sale.client.name }}</td>
                <td>{{ item.sale.client.city.city }}</td>
              </tr>
            </tbody>
          </v-table>
          <v-alert v-else type="info" class="mt-2">No data</v-alert>
        </v-col>
  
        <!-- 60-90 Days -->
        <v-col cols="12">
          <h3 class="section-title">60-90 Days</h3>
          <v-table v-if="range60to90.length" class="modern-table rounded-lg">
            <thead>
              <tr>
                <th class="text-left">#</th>
                <th class="text-left">Equipment</th>
                <th class="text-left">Due Date (days remaining)</th>
                <th class="text-left">Client</th>
                <th class="text-left">City</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, i) in range60to90" :key="item.id" class="table-row-hover">
                <td>{{ i + 1 }}</td>
                <td>{{ item.sale.equipment.equipmentName }}</td>
                <td>
                  {{ item.maintenancePeriod.split('T')[0] }}
                  ({{ getDaysRemaining(item.maintenancePeriod.split('T')[0]) }})
                </td>
                <td>{{ item.sale.client.name }}</td>
                <td>{{ item.sale.client.city.city }}</td>
              </tr>
            </tbody>
          </v-table>
          <v-alert v-else type="info" class="mt-2">No data</v-alert>
        </v-col>
  
        <!-- 90+ Days -->
        <v-col cols="12">
          <h3 class="section-title">90+ Days</h3>
          <v-table v-if="range90plus.length" class="modern-table rounded-lg">
            <thead>
              <tr>
                <th class="text-left">#</th>
                <th class="text-left">Equipment</th>
                <th class="text-left">Due Date (days remaining)</th>
                <th class="text-left">Client</th>
                <th class="text-left">City</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, i) in range90plus" :key="item.id" class="table-row-hover">
                <td>{{ i + 1 }}</td>
                <td>{{ item.sale.equipment.equipmentName }}</td>
                <td>
                  {{ item.maintenancePeriod.split('T')[0] }}
                  ({{ getDaysRemaining(item.maintenancePeriod.split('T')[0]) }})
                </td>
                <td>{{ item.sale.client.name }}</td>
                <td>{{ item.sale.client.city.city }}</td>
              </tr>
            </tbody>
          </v-table>
          <v-alert v-else type="info" class="mt-2">No data</v-alert>
        </v-col>
      </v-row>
  
      <!-- Pagination -->
      <paginationVue v-if="hasData" :length="preventiveTotalPage" @changePage="chanegePage" class="pagination" />
  
      <!-- Delete Confirmation Dialog -->
      <deleteWarnVue :visible="delete_dialog" @close="delete_dialog = false" :item="deleteValue" :log="true" @delete="update" class="dialog-animation" />
    </v-container>
  </template>
  
  <script>
  import { mapActions, mapState } from 'vuex';
  import moment from 'moment';
  import deleteWarnVue from '@/components/deleteWarn.vue';
  import paginationVue from '@/components/pagination.vue';
  import searchAndFilterToolbar from '@/components/searchAndFilterToolbar.vue';
  
  export default {
    name: 'preventiveReminder',
    components: {
      searchAndFilterToolbar,
      deleteWarnVue,
      paginationVue,
    },
    data() {
      return {
        breadcrumbs: [
          { text: 'Home', disabled: false, href: '/' },
          { text: 'Preventive Maintenance Reminder', disabled: true, href: '/preventiveReminders' },
        ],
        reminder_text: '',
        delete_dialog: false,
        deleteValue: {},
        filterDisplay: false,
        searchQuery: '',
        selectedClient: null,
        selectedEquipment: null,
        selectedCity: null,
      };
    },
    computed: {
      ...mapState('PreventiveReminders', ['preventiveList', 'preventiveTotalPage']),
      ...mapState('city', ['cityList']),
      ...mapState('equipments', ['allEquipments']),
      ...mapState('clients', ['clientList']),
      hasData() {
        return Array.isArray(this.preventiveList) && this.preventiveList.length > 0;
      },
      filteredList() {
        if (!Array.isArray(this.preventiveList)) {
          console.warn('preventiveList is not an array:', this.preventiveList);
          return [];
        }
        return this.preventiveList.filter(item => {
          if (!item || !item.sale || !item.sale.client || !item.sale.equipment) {
            return false;
          }
          const searchMatch = !this.searchQuery ||
            (item.sale.client.name || '').toLowerCase().includes(this.searchQuery.toLowerCase()) ||
            (item.sale.equipment.equipmentName || '').toLowerCase().includes(this.searchQuery.toLowerCase());
          const clientMatch = !this.selectedClient || item.sale.client.id === this.selectedClient;
          const equipmentMatch = !this.selectedEquipment || item.sale.equipment.id === this.selectedEquipment;
          const cityMatch = !this.selectedCity || item.sale.client.city.id === this.selectedCity;
          return searchMatch && clientMatch && equipmentMatch && cityMatch;
        });
      },
      range0to30() {
        return this.filteredList.filter(item => {
          const days = this.getDaysNumeric(item.maintenancePeriod.split('T')[0]);
          return Math.abs(days) <= 30;
        });
      },
      range30to60() {
        return this.filteredList.filter(item => {
          const days = this.getDaysNumeric(item.maintenancePeriod.split('T')[0]);
          return days > 30 && days <= 60;
        });
      },
      range60to90() {
        return this.filteredList.filter(item => {
          const days = this.getDaysNumeric(item.maintenancePeriod.split('T')[0]);
          return days > 60 && days <= 90;
        });
      },
      range90plus() {
        return this.filteredList.filter(item => {
          const days = this.getDaysNumeric(item.maintenancePeriod.split('T')[0]);
          return days > 90;
        });
      },
    },
    methods: {
      ...mapActions('PreventiveReminders', ['GET_PREVENTIVE_LIST', 'UPDATE_SERVICE_PREVENTIVE']),
      ...mapActions('city', ['GET_CITY_LIST']),
      ...mapActions('equipments', ['GET_ALL_EQUIPMENT']),
      ...mapActions('clients', ['GET_CLIENT_LIST']),
      getDaysNumeric(date) {
        const dueDate = moment(date);
        const today = moment();
        return dueDate.diff(today, 'days');
      },
      getDaysDisplay(date) {
        const days = this.getDaysNumeric(date);
        return days >= 0 ? `in ${days} days` : `${Math.abs(days)} days ago`;
      },
      getDaysRemaining(date) {
        const days = this.getDaysNumeric(date);
        return `${days} days remaining`;
      },
      searchByName(val) {
        this.searchQuery = val || '';
      },
      applyFilters() {
        this.searchByName(this.searchQuery);
      },
      resetFilters() {
        this.searchQuery = '';
        this.selectedClient = null;
        this.selectedEquipment = null;
        this.selectedCity = null;
        this.applyFilters();
      },
      updateService(item) {
        this.delete_dialog = true;
        this.deleteValue = item;
      },
      async update(item) {
        await this.UPDATE_SERVICE_PREVENTIVE(item);
        this.delete_dialog = false;
        await this.GET_PREVENTIVE_LIST();
      },
      chanegePage(page) {
        const query = {
          page: page,
          size: 15,
        };
        this.GET_PREVENTIVE_LIST(query);
      },
      openAddService() {
        console.log('Open Add Service');
      },
      logPreventiveListStructure() {
        console.log('Preventive List Structure:', {
          type: typeof this.preventiveList,
          isArray: Array.isArray(this.preventiveList),
          value: this.preventiveList,
        });
      },
    },
    mounted() {
      this.GET_PREVENTIVE_LIST();
      this.GET_CITY_LIST();
      this.GET_ALL_EQUIPMENT();
      this.GET_CLIENT_LIST();
      this.$nextTick(() => {
        this.logPreventiveListStructure();
      });
    },
  };
  </script>
  
  <style scoped>
  /* Base Container Styling */
  .hmmsdashboard {
    min-height: 100vh;
    padding: 16px;
    position: relative;
    overflow-x: hidden;
  }
  
  /* Category Title */
  .category-title {
    font-size: 1.8rem;
    font-weight: bold;
    background: linear-gradient(45deg, #FF5733, #2A4066);
    -webkit-background-clip: text;
    color: transparent;
    animation: slideInLeft 0.8s ease-out;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  }
  
  /* Section Titles */
  .section-title {
    font-size: 1.4rem;
    font-weight: 600;
    color: #2A4066;
    margin-bottom: 12px;
    animation: fadeIn 0.8s ease-out;
  }
  
  /* Breadcrumbs */
  .breadcrumbs-container {
    background: linear-gradient(90deg, #FF5733, #2A4066);
    border-radius: 8px;
    padding: 8px 12px;
    box-shadow: 0 4px 10px rgba(255, 87, 51, 0.3);
    animation: fadeInDown 0.6s ease-out;
    margin-top: 8px;
  }
  
  .custom-breadcrumb-item {
    color: #fff;
    font-weight: 600;
    font-size: 0.9rem;
    transition: all 0.3s ease;
  }
  
  .custom-breadcrumb-item:hover {
    color: #E8ECEF;
    transform: scale(1.05);
  }
  
  /* Toolbar */
  .toolbar {
    margin-top: 12px;
    animation: fadeInUp 0.8s ease-out;
  }
  
  /* Filter Container */
  .filter-container {
    background: rgba(255, 255, 255, 0.95);
    border-radius: 8px;
    padding: 12px;
    box-shadow: 0 4px 12px rgba(255, 87, 51, 0.2);
    animation: fadeIn 0.8s ease-out;
  }
  
  .filter-btn {
    border-radius: 6px;
    transition: all 0.3s ease;
  }
  
  .filter-btn:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 12px rgba(255, 87, 51, 0.3);
  }
  
  /* Modern Table */
  .modern-table {
    background: #fff;
    box-shadow: 0 4px 20px rgba(255, 87, 51, 0.2);
    border-radius: 12px;
    overflow-x: auto;
    animation: fadeIn 1s ease-out;
    font-family: 'Montserrat', sans-serif !important;
  }
  
  .modern-table th {
    background: linear-gradient(90deg, #2A4066, #FF5733);
    color: #fff;
    padding: 12px 8px;
    font-weight: 600;
    text-transform: uppercase;
    white-space: nowrap;
    font-size: 0.9rem;
  }
  
  .modern-table td {
    padding: 12px 8px;
    font-size: 0.9rem;
    font-weight: 500;
  }
  
  .table-row-hover {
    transition: all 0.3s ease;
  }
  
  .table-row-hover:hover {
    background: linear-gradient(90deg, #F5F7FA, #E8ECEF);
    transform: translateY(-2px);
    box-shadow: 0 2px 8px rgba(255, 87, 51, 0.2);
  }
  
  .modern-table tbody tr:nth-of-type(odd) {
    background-color: rgba(0, 0, 0, 0.05);
  }
  
  /* Pagination */
  .pagination {
    margin-top: 16px;
    animation: fadeInUp 0.8s ease-out;
  }
  
  /* Snackbar and Dialog */
  .dialog-animation /deep/ .v-dialog {
    background: linear-gradient(135deg, rgba(255, 255, 255, 0.95), rgba(245, 247, 250, 0.9));
    backdrop-filter: blur(8px);
    border-radius: 12px;
    box-shadow: 0 8px 25px rgba(255, 87, 51, 0.3);
    padding: 12px;
    animation: zoomInPop 0.5s ease-out;
    max-width: 90%;
    width: 400px;
  }
  
  .dialog-animation /deep/ .v-card-title {
    font-size: 1.4rem;
    font-weight: bold;
    color: #2A4066;
    background: linear-gradient(45deg, #FF5733, #2A4066);
    -webkit-background-clip: text;
    color: transparent;
    text-align: center;
  }
  
  .dialog-animation /deep/ .v-text-field .v-input__control {
    border-radius: 6px;
    background: rgba(255, 255, 255, 0.9);
    transition: all 0.3s ease;
  }
  
  .dialog-animation /deep/ .v-text-field .v-input__control:hover {
    background: rgba(255, 235, 230, 0.95);
    box-shadow: 0 2px 8px rgba(255, 87, 51, 0.2);
  }
  
  .dialog-animation /deep/ .v-btn {
    border-radius: 6px;
    transition: all 0.3s ease;
  }
  
  .dialog-animation /deep/ .v-btn--primary {
    background: linear-gradient(45deg, #FF5733, #2A4066);
    color: #fff;
  }
  
  .dialog-animation /deep/ .v-btn--primary:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 12px rgba(255, 87, 51, 0.3);
  }
  
  /* Transition for Filter */
  .slide-fade-enter-active, .slide-fade-leave-active {
    transition: all 0.3s ease;
  }
  
  .slide-fade-enter-from, .slide-fade-leave-to {
    transform: translateY(-20px);
    opacity: 0;
  }
  
  /* CSS Keyframe Animations */
  @keyframes zoomInPop {
    0% { opacity: 0; transform: scale(0.7); }
    60% { opacity: 1; transform: scale(1.05); }
    100% { transform: scale(1); opacity: 1; }
  }
  
  @keyframes slideInLeft {
    0% { opacity: 0; transform: translateX(-100px); }
    100% { opacity: 1; transform: translateX(0); }
  }
  
  @keyframes fadeInDown {
    0% { opacity: 0; transform: translateY(-50px); }
    100% { opacity: 1; transform: translateY(0); }
  }
  
  @keyframes fadeInUp {
    0% { opacity: 0; transform: translateY(50px); }
    100% { opacity: 1; transform: translateY(0); }
  }
  
  @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  
  /* Responsive Design */
  @media (max-width: 960px) {
    .hmmsdashboard {
      padding: 12px;
    }
  
    .category-title {
      font-size: 1.5rem;
    }
  
    .section-title {
      font-size: 1.2rem;
    }
  
    .breadcrumbs-container {
      padding: 6px 10px;
    }
  
    .custom-breadcrumb-item {
      font-size: 0.8rem;
    }
  
    .toolbar {
      margin-top: 10px;
    }
  
    .filter-container {
      padding: 10px;
    }
  
    .modern-table {
      margin-top: 12px;
      display: block;
      overflow-x: auto;
      white-space: nowrap;
    }
  
    .modern-table th, .modern-table td {
      padding: 10px 6px;
      font-size: 0.8rem;
      min-width: 100px;
    }
  
    .modern-table th:first-child, .modern-table td:first-child {
      min-width: 40px;
    }
  
    .pagination {
      margin-top: 12px;
    }
  
    .dialog-animation /deep/ .v-dialog {
      width: 350px;
      padding: 10px;
    }
  
    .dialog-animation /deep/ .v-card-title {
      font-size: 1.3rem;
    }
  
    .dialog-animation /deep/ .v-btn {
      padding: 5px 12px;
    }
  }
  
  @media (max-width: 600px) {
    .hmmsdashboard {
      padding: 8px;
    }
  
    .category-title {
      font-size: 1.2rem;
    }
  
    .section-title {
      font-size: 1rem;
    }
  
    .breadcrumbs-container {
      padding: 4px 8px;
    }
  
    .custom-breadcrumb-item {
      font-size: 0.7rem;
    }
  
    .toolbar {
      margin-top: 8px;
    }
  
    .filter-container {
      padding: 8px;
    }
  
    .filter-btn {
      font-size: 0.8rem;
      padding: 6px 12px;
    }
  
    .modern-table {
      margin-top: 10px;
    }
  
    .modern-table th, .modern-table td {
      padding: 8px 4px;
      font-size: 0.7rem;
      min-width: 80px;
    }
  
    .modern-table th:first-child, .modern-table td:first-child {
      min-width: 30px;
    }
  
    .v-alert {
      font-size: 0.8rem;
      padding: 8px;
    }
  
    .pagination {
      margin-top: 10px;
    }
  
    .dialog-animation /deep/ .v-dialog {
      width: 90%;
      padding: 8px;
    }
  
    .dialog-animation /deep/ .v-card-title {
      font-size: 1.1rem;
    }
  
    .dialog-animation /deep/ .v-text-field .v-input__control {
      font-size: 0.8rem;
    }
  
    .dialog-animation /deep/ .v-btn {
      padding: 4px 8px;
      font-size: 0.7rem;
    }
  }
  </style>