<template>
    <div class="text-center">
        <v-dialog v-model="show" transition="dialog-top-transition" activator="parent" :max-width="isMobile ? '100%' : '900'" :fullscreen="isMobile" scrollable>
            <v-card class="dialog-card elevation-5">
                <v-card-title class="dialog-title">
                    {{ title }}
                    <v-spacer></v-spacer>
                    <v-btn icon small @click="show = false" class="close-btn">
                        <v-icon style="color: black;">mdi-close</v-icon>
                    </v-btn>
                </v-card-title>

                <v-card-text class="form-container">
                    <v-container fluid>
                        <v-row dense>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-autocomplete color="#7b1fa2" variant="outlined" density="compact" v-model:search="search"
                                    v-model="selcetdClient" clearable :items="clientList" item-title="name" item-value="id"
                                    label="Client" class="custom-field" prepend-inner-icon="mdi-account"></v-autocomplete>
                                <span v-if="v$.selcetdClient.$error" class="error-text">
                                    Client is required
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-autocomplete color="#7b1fa2" variant="outlined" density="compact" v-model="selcetdCategory"
                                    clearable :items="categoryList" item-title="categoryName" item-value="id"
                                    label="Category" class="custom-field" prepend-inner-icon="mdi-shape"></v-autocomplete>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-autocomplete color="#7b1fa2" variant="outlined" density="compact" v-model="selcetdSubCategory"
                                    clearable :items="availSubCatList" item-title="subcategoryName" item-value="id"
                                    label="Sub Category" class="custom-field" prepend-inner-icon="mdi-shape-outline"
                                    @click="getSubcatList"></v-autocomplete>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-autocomplete color="#7b1fa2" variant="outlined" density="compact" v-model="selcetedEquipment"
                                    clearable :items="availableEquilist" item-title="equipmentName" item-value="id"
                                    label="Equipment" class="custom-field" prepend-inner-icon="mdi-tools"
                                    @click="getEquiList"></v-autocomplete>
                                <span v-if="v$.selcetedEquipment.$error" class="error-text">
                                    Equipment is required
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-text-field color="#7b1fa2" label="Maintenance Period" density="compact" v-model="MaintenancePeriod"
                                    variant="outlined" class="custom-field" prepend-inner-icon="mdi-calendar-clock"></v-text-field>
                                <span v-if="v$.MaintenancePeriod.$error" class="error-text">
                                    {{ v$.MaintenancePeriod.$errors[0].$message }}
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-text-field color="#7b1fa2" label="Serial No" density="compact" v-model="serialNo"
                                    variant="outlined" class="custom-field" prepend-inner-icon="mdi-barcode"></v-text-field>
                                <span v-if="v$.serialNo.$error" class="error-text">
                                    Serial No is required
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-text-field color="#7b1fa2" label="Installation Date" type="date" density="compact"
                                    v-model="InstallationDate" variant="outlined" class="custom-field"
                                    prepend-inner-icon="mdi-calendar-start"></v-text-field>
                                <span v-if="v$.InstallationDate.$error" class="error-text">
                                    Installation Date is required
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-text-field color="#7b1fa2" label="Last Maintenance Date" type="date" density="compact"
                                    v-model="lastMainDate" variant="outlined" class="custom-field"
                                    prepend-inner-icon="mdi-calendar-check"></v-text-field>
                                <span v-if="v$.lastMainDate.$error" class="error-text">
                                    Last Maintenance Date is required
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-text-field color="#7b1fa2" label="Warranty Expiry Date" type="date" density="compact"
                                    v-model="WarrantyDate" variant="outlined" class="custom-field"
                                    prepend-inner-icon="mdi-calendar-end"></v-text-field>
                                <span v-if="v$.WarrantyDate.$error" class="error-text">
                                    Warranty Expiry Date is required
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-file-input color="#7b1fa2" prepend-inner-icon="mdi-file-pdf-box" density="compact" clearable
                                    variant="outlined" v-model="invoice" show-size label="Invoice" class="custom-field" multiple="false"></v-file-input>
                                <span v-if="v$.invoice.$error" class="error-text">
                                    Invoice is required
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-file-input color="#7b1fa2" prepend-inner-icon="mdi-file-pdf-box" density="compact" clearable
                                    variant="outlined" v-model="amc" show-size label="AMC Document" class="custom-field" multiple="false"></v-file-input>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-file-input color="#7b1fa2" prepend-inner-icon="mdi-file-pdf-box" density="compact" clearable
                                    variant="outlined" v-model="installationReport" show-size label="Installation Report"
                                    class="custom-field" multiple="false"></v-file-input>
                                <span v-if="v$.installationReport.$error" class="error-text">
                                    Installation Report is required
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-file-input color="#7b1fa2" prepend-inner-icon="mdi-file-pdf-box" density="compact" clearable
                                    variant="outlined" v-model="warranty" show-size label="Warranty Document"
                                    class="custom-field" multiple="false"></v-file-input>
                                <span v-if="v$.warranty.$error" class="error-text">
                                    Warranty Document is required
                                </span>
                            </v-col>
                            <v-col cols="12" :sm="isMobile ? 12 : 4">
                                <v-file-input color="#7b1fa2" prepend-inner-icon="mdi-file-pdf-box" density="compact" clearable
                                    variant="outlined" v-model="maintenance" show-size label="Maintenance Contract"
                                    class="custom-field" multiple="false"></v-file-input>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card-text>
                <v-card-actions class="action-container">
                    <v-btn class="action-btn cancel-btn" @click="show = false">
                        Cancel
                    </v-btn>
                    <v-btn class="action-btn save-btn" @click="addSale()">
                        Save
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required, numeric, decimal, maxLength, email } from "@vuelidate/validators";
import { mapActions, mapState } from 'vuex'
export default {

    name: 'addEquipment',
    data() {
        return {
            v$: useVuelidate(),
            dialog: false,
            serialNo: '',
            MaintenancePeriod: '',
            selcetdClient: '',
            selcetdCategory: '',
            selcetdSubCategory: '',
            selcetedEquipment: '',
            invoice: '',
            amc: '',
            installationReport: '',
            warranty: '',
            maintenance: '',
            // InstallationDate: new Date().toISOString().substr(0, 10),
            InstallationDate: '',
            WarrantyDate: '',
            // orderDate: '',
            availSubCatList: [],
            availableEquilist: [],
            search: '',
            lastMainDate: '',
        }
    },
    watch: {
        search(val) {
            this.GET_CLIENT_LIST({ name: val })
        },
        InstallationDate(val) {
            this.lastMainDate = val;
        }
    },
    validations() {
        return {
            selcetdClient: { required },
            selcetedEquipment: { required },
            MaintenancePeriod: { required, numeric },
            serialNo: { required },
            InstallationDate: { required },
            WarrantyDate: { required },
            invoice: { required },
            installationReport: { required },
            warranty: { required },
            // orderDate: { required },
            lastMainDate: { required },
        }
    },
    props: {
        visible: {
            type: Boolean,
        },
        title: {
            type: String
        }

    },
    computed: {
        ...mapState('clients', ['clientList']),
        ...mapState('category', ['categoryList']),
        ...mapState('subCategory', ['subcatList']),
        ...mapState('equipments', ['allEquipments']),
        isMobile() {
            return this.$vuetify.display.smAndDown;
        },

        show: {
            get() {
                return this.visible
            },
            set(value) {
                if (!value) {
                    this.$emit('close')
                }
            }
        }
    },
    methods: {


        ...mapActions('clients', ['GET_CLIENT_LIST', 'ADD_CLIENT', 'DELETE_CLIENT', 'GET_CLIENT_BY_ID', 'UPDATE_CLIENT']),
        ...mapActions('category', ['GET_ALL_CATEGORY', 'ADD_CATEGORY', 'UPDATE_CATEGORY', 'DELETE_CATEGORY']),
        ...mapActions('subCategory', ['GET_SUB_CATEGORY', 'GET_ALL_SUBCATEGORY']),
        ...mapActions('equipments', ['GET_EQUIPMENT', 'GET_ALL_EQUIPMENT']),
        addCat() {

            const payload = {
                name: this.name,
                image: this.image
            }

            this.$emit('save', payload);
            this.name = '';
            this.image = '';
        },
        getSubcatList() {
            this.availSubCatList = []
            if (!this.selcetdCategory) {
                this.availSubCatList = this.subcatList
            } else {
                const list = this.$store.getters["subCategory/get_all_sub_cat_with_cat_id"](this.selcetdCategory);
                this.availSubCatList = list
            }


        },
        getEquiList() {
            this.availableEquilist = []
            if (!this.selcetdSubCategory) {
                this.availableEquilist = this.allEquipments
            } else {
                const list = this.$store.getters["equipments/get_all_equipment_with_sub_catId"](this.selcetdSubCategory);
                this.availableEquilist = list
            }
        },
        addSale() {
            this.v$.$validate();

            if (!this.v$.$error) {
                const payloads = {
                    serialNo: this.serialNo,
                    equipmentId: this.selcetedEquipment,
                    clientId: this.selcetdClient,
                    maintenancePeriod: this.MaintenancePeriod,
                    installationDate: this.InstallationDate,
                    warrantyDate: this.WarrantyDate,
                    invoice: this.invoice,
                    installationReport: this.installationReport,
                    warrantyDocument: this.warranty,
                    maintenanceContract: this.maintenance,
                    annualMaintenanceContract: this.amc,
                    // orderDate: this.orderDate,
                    LastMaintenanceDate: this.lastMainDate
                }
                this.$emit('save', payloads)
                this.serialNo = '';
                this.selcetedEquipment = '';
                this.selcetdClient = '';
                this.MaintenancePeriod = '';
                this.InstallationDate = '';
                this.WarrantyDate = '';
                this.invoice = '';
                this.installationReport = '';
                this.warranty = '';
                this.maintenance = '';
                this.amc = '';
                // this.orderDat = '';
                this.lastMainDate = '';
                this.v$.$reset();
            }
        }
    },
    created() {
        this.name = '';
        this.image = '';
        this.modelNo = '';
        const query = {
            size: 150
        }
        this.GET_CLIENT_LIST(query);
        this.GET_ALL_CATEGORY(query);
        this.GET_ALL_SUBCATEGORY(query);
        this.GET_ALL_EQUIPMENT();
    }
}
</script>
<style scoped>
.dialog-card {
    max-height: 90vh;
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    background: #ffffff;
}

.close-btn {
    background: rgba(255, 255, 255, 0.2);
    color: white;
}

.close-btn:hover {
    background: rgba(255, 255, 255, 0.3);
}

.form-container {
    flex: 1;
    overflow-y: auto;
    padding: 24px;
    background: #fafafa;
}

.custom-field {
    border-radius: 8px;
    background: #ffffff;
    transition: all 0.3s ease;
}

.custom-field:focus-within {
    box-shadow: 0 0 8px rgba(123, 31, 162, 0.3);
}

.action-container {
    background: #ffffff;
    border-top: 1px solid #eee;
    padding: 16px 24px;
    justify-content: flex-end;
    gap: 12px;
}

.action-btn {
    min-width: 120px;
    border-radius: 25px;
    text-transform: uppercase;
    font-weight: 600;
    padding: 8px 24px;
    transition: all 0.3s ease;
}

.cancel-btn {
    background: #e0e0e0;
    color: #424242;
}

.cancel-btn:hover {
    background: #d0d0d0;
}

.save-btn {
    background: linear-gradient(45deg, #d46f1c, #7b1fa2);
    color: white;
}

.save-btn:hover {
    background: linear-gradient(45deg, #8e24aa, #6a1b9a);
}

.error-text {
    color: #d32f2f;
    font-size: 0.75rem;
    margin-top: 4px;
    display: block;
}

@media (min-width: 601px) {
    .dialog-card {
        margin: 24px;
    }

    .v-row {
        margin: -12px;
    }

    .v-col {
        padding: 12px;
    }

    .custom-field {
        font-size: 0.95rem;
    }
}

@media (max-width: 600px) {
    .dialog-card {
        max-height: 100vh;
        border-radius: 0;
        margin: 0;
    }

    .dialog-title {
        font-size: 1.1rem;
        padding: 12px 16px;
    }

    .form-container {
        padding: 16px;
    }

    .v-row {
        margin: -8px;
    }

    .v-col {
        padding: 8px;
    }

    .custom-field {
        font-size: 0.9rem;
    }

    .action-container {
        flex-direction: column;
        padding: 16px;
        gap: 8px;
    }

    .action-btn {
        width: 100%;
        padding: 10px;
    }

    .error-text {
        font-size: 0.7rem;
    }
}
</style>    