<template>
    <v-row class="justify-center" style="max-width: 2571px; margin: auto;">
        <v-col cols="12" md="3" sm="6" v-for="item in cat" :key="item.id">
            <v-card 
                class="text-center card-container" 
                rounded="lg" 
                height="300" 
                elevation="2"
                max-width="600"
                @click="toSubcat(item)"
            >
                <v-card-title class="px-2 admin-controls" style="font-size: 1rem;">
                    <v-spacer></v-spacer>
                    <v-btn flat size="150" v-if="isAdmin()">
                        <v-icon style="color: black;">mdi-dots-vertical</v-icon>
                        <v-menu activator="parent">
                            <v-list class="pa-0">
                                <v-list-item>
                                    <v-list-item-title @click="openEditDailog(item)">
                                        <v-icon size="20">mdi-pencil-outline</v-icon> Edit
                                    </v-list-item-title>
                                </v-list-item>
                                <v-list-item @click="deleteItem(item)">
                                    <v-list-item-title>
                                        <v-icon size="20">mdi-trash-can-outline</v-icon> Delete
                                    </v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-menu>
                    </v-btn>
                </v-card-title>
                
                <div class="image-container">
                    <v-img 
                        :src="item.categoryImage || item.subcategoryImage" 
                        class="card-image"
                        cover
                        height="100%"
                        width="100%"
                    ></v-img>
                </div>
                
                <v-card-header class="justify-center flex-column card-footer">
                    <div class="card_text_sub text-h6" style="color:#00284a;">
                        {{ item.categoryName || item.subcategoryName }}
                    </div>
                </v-card-header>
            </v-card>
        </v-col>
    </v-row>
    <editCategoryVue 
        :visible="edit_dialog" 
        @close="edit_dialog = false" 
        :item="cat_name" 
        @update="upadateCategory" 
    />
    <deleteWarnVue 
        :visible="delete_dialog" 
        @close="delete_dialog = false" 
        :item="deleteValue" 
        @delete="deleteCat" 
    />
</template>

<script>
import editCategoryVue from './categories/editCategory.vue';
import deleteWarnVue from './deleteWarn.vue';

export default {
    name: 'catCard',
    components: {
        editCategoryVue,
        deleteWarnVue
    },
    props: {
        cat: {
            type: Array
        }
    },
    data: () => ({
        edit_dialog: false,
        delete_dialog: false,
        deleteValue: {},
        cat_name: '',
        cat_id: '',
        items: [
            { title: 'Delete' },
            { title: '' },
        ],
    }),
    methods: {
        isAdmin() {
            const user = JSON.parse(localStorage.getItem('user'));
            if (user) {
                return user.role[0] === 'admin';
            }
            return false;
        },
        toSubcat(item) {
            this.$emit("clickOnCard", item);
        },
        deleteItem(item) {
            this.deleteValue = item;
            this.delete_dialog = true;
        },
        deleteCat(item) {
            this.$emit('delteItem', item);
            this.delete_dialog = false;
        },
        openEditDailog(item) {
            this.cat_name = item.categoryName || item.subcategoryName;
            this.cat_id = item.id;
            this.edit_dialog = true;
        },
        upadateCategory(item) {
            this.$emit('editItem', { item: item, id: this.cat_id });
            this.edit_dialog = false;
        }
    }
}
</script>

<style scoped>
.card-container {
    font-family: Montserrat, sans-serif !important;
    transition: all 0.3s ease;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
}

.image-container {
    flex: 1;
    overflow: hidden;
    position: relative;
    height: 70%;
}

.card-image {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    transition: transform 0.3s ease;
}

.card-container:hover .card-image {
    transform: scale(1.05);
}

.card-footer {
    height: 30%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
}

.card_text_sub {
    font-weight: 600;
    width: 100%;
    text-align: center;
    line-height: 1.2;
    text-transform: capitalize;
    letter-spacing: 0.5px;
    padding: 0 8px;
}

.admin-controls {
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 1;
    background: rgba(255,255,255,0.9);
    border-radius: 50%;
}
</style>